<template>
  <div class="g-extension scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
      <div class="m-title">
        <div class="s-img">
          <img :src="user.pic" alt="">
        </div>
        <div class="s-msg">
          <div class="name">{{user.username}}</div>
          <div class="level">{{user.vip==1?'普通会员':user.vip==2?'vip会员':'超级VIP会员'}}</div>
        </div>
      </div>
      <div class="m-Profit">
        <div class="s-today">
          <div class="title">今日收益</div>
          <div class="jf">积分：{{today.integral||0}}</div>
          <div class="yb">元宝：{{today.ingots||0}}</div>
        </div>
        <div class="s-all">
          <div class="title">累计收益</div>
          <div class="jf">积分：{{all.integral||0}}</div>
          <div class="yb">元宝：{{all.ingots||0}}</div>
        </div>
      </div>
      <div class="m-border"></div>
      <div class="m-bill">
        <div class="s-title">收益明细</div>
        <div class="s-details" v-for="(v,i) in list">
          <div class="img">
            <img v-lazy="v.subordinateHead" alt="">
          </div>
          <div class="usermsg">
            <span class="level">{{v.rank==1?'一级':'二级'}}</span>
            <span class="phone">{{v.subordinateId}}</span>
          </div>
          <div class="orderDetails">
            <span class="orderNumber">订单号：{{v.orderNumber}}</span>
            <span class="time">{{v.createTimeStr}}</span>
          </div>
          <div class="earn">
            <span>{{v.currency==1?'积分 ':'元宝'}}</span>
            <span>{{v.money}}</span>
          </div>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: "extension",
    data(){
      return {
        list:[],
        today:{},
        all:{},
        user:{},
        options:{/*滚动配置*/
          scrollbar:{fade:true},
          pullUpLoad:{txt:{ noMore: '没有更多数据了' }},
          pullDownRefresh:{stopTime:0,txt:' '}
        },
        bit:{
          page:1,
          username:sessionStorage.getItem('userid'),
        }
      }
    },
    created(){
      this.userMsg();
      this.billList();
    },
    methods:{
      userMsg(){
        this.$post(this.$bee+'/api/user/userDetail',{username:this.bit.username}).then((data)=>{
          if(data.code==200){
            this.user=data.data.user;
          }else{
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      billList(){
        this.$post(this.$bee+'/api/user/listPromotion',this.bit).then((data)=> {
          if (data.code == 200&&data.data.records.length!=0) {
            this.today=data.data.today;
            this.all=data.data.all;
            data.data.records.forEach((i)=>{
              this.list.push(i)
            })
            this.bit.page++;
          } else {
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {
        this.bit.page=1;
        this.list=[];
        this.billList();
      },
      onPullingUp() {
        this.billList();
      }
    }
  }
</script>

<style scoped lang="stylus">
  .m-title
    height 85px
    width 100%
    background-image: linear-gradient(0deg, #fb5757 0%, #ee5050 50%, #e04848 100%), linear-gradient(#fb5757, #fb5757);
    background-blend-mode: normal, normal;
    display flex
    flex-wrap wrap
    box-sizing border-box
    padding 8px 25px 18px 25px
    .s-img
      width 58px
      height 58px
      border-radius 50%
      overflow hidden
      img
        width 100%
        height 100%
    .s-msg
      width calc(100% - 78px)
      text-align left
      padding-left 20px
      .name
        color #fff
        font-size 15px
        margin-top 16px
      .level
        color #fff
        font-size 12px
        margin-top 10px

  .m-Profit
    display flex
    & > div
      flex 1
      padding-top 24px
      &:nth-child(1)
        position relative
        &:after
          content ''
          position absolute
          right 0
          top 30px
          width: 1px;
          height: 45px;
          background-color: #d9d9d9;
      .title
        color: #969696;
        font-size 19px
        margin-bottom 11px
      .jf, .yb
        color: #e0705a;
        font-size 16px
      .jf
        margin-bottom 8px

  .m-border
    width 350px
    height 6px
    margin 9px auto 0
    background-color: #efefef;
  .m-bill
    padding 0 13px
    .s-title
      color: #969696;
      font-size 15px
      height 30px
      line-height 30px
      border-bottom 1px solid #efefef
    .s-details
      padding 5px 0
      display flex
      flex-wrap wrap
      border-bottom 4px solid #efefef
      .img
        width 40px
        height 40px
        img
          width 100%
          height 100%
      .orderDetails,.usermsg
        &:after
          position absolute
          right 0
          top 6px
          content ''
          width 1px
          height 30px
          background-color: #d9d9d9;
      .usermsg
        width 60px
        font-size 11px
        color: #3d3d3d;
        text-align left
        margin-left 5px
        position relative
        .level,.phone
          display block
        .level
          margin-top 5px
        .phone
          margin-top 7px
      .orderDetails
        width 190px
        position relative
        .orderNumber,.time
          display block
          font-size 12px
          color: #585858;
          text-align left
          padding-left 10px
        .orderNumber
          margin-top 5px
        .time
          margin-top 7px
      .earn
        span
          display block
          color: #585858;
          font-size 12px
          text-align center
          width 50px
          &:nth-child(1)
            margin-top 5px
          &:nth-child(2)
            margin-top 7px
</style>
